import { LitElement, html } from "lit";
import { customElement, property } from "lit/decorators.js";

@customElement("dfci-page-header")
export class DfciPageHeader extends LitElement {
    @property({ type: String, attribute: "image-src" }) imageSrc?: string;
    @property({ type: String, attribute: "image-alt" }) imageAlt?: string;

    // static styles = unsafeCSS(styles);

    render() {
        return html`
            <style>
                .page-header {
                    position: relative;
                    min-height: 400px;
                }

                .page-header .swoosh {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                }

                .page-header figure {
                    margin: 0;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: flex;

                    overflow: hidden;
                }

                .page-header figure img {
                    width: 100%;
                    height: 100%;
                    transform: scale(1.1);
                    object-fit: cover;
                    display: flex;
                }

                ::slotted([slot="heading"]) {
                    position: absolute;
                    bottom: 1rem;
                    width: 100%;
                    margin: unset;

                    font-family: "Arial";
                    font-weight: 700;
                    font-size: 8rem;
                    text-align: center;
                    line-height: 105%;
                    display: block;
                    align-items: center;

                    color: #ffffff;
                }
            </style>
            <hgroup class="page-header">
                <figure>
                    <img src=${this.imageSrc} alt=${this.imageAlt} />
                </figure>
                <svg
                    class="swoosh"
                    viewBox="0 0 1440 145"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M0 145C591 41.5209 1284 0 1440 0V145H0Z"
                        fill="#00629B"
                    />
                </svg>

                <slot name="heading"></slot>
            </hgroup>
        `;
    }
}

declare global {
    interface HTMLElementTagNameMap {
        "dfci-page-header": DfciPageHeader;
    }
}
